<template>
  <div class="account">
    <van-nav-bar title="账号管理" left-arrow @click-left="back" />

    <van-cell-group>
      <van-cell class="v-cell" title="头像">
        <div class="clearfix">
          <div class="fr user-img">
            <img class="auto-img" :src="userInfo.userImg" alt />
            <van-uploader class="upload-box" :after-read="uploadFile" />
          </div>
        </div>
      </van-cell>
      <van-cell class="v-cell" title="用户id" :value="userInfo.userId"></van-cell>
      <van-cell class="v-cell" title="手机号" :value="userInfo.phone"></van-cell>
      <van-cell class="v-cell" title="昵称">
        <div class="clearfix">
          <div class="fr">
            <van-field
              @blur="updateUserInfo({key: 'nickName', value: userInfo.nickName, url: 'http://www.kangliuyong.com:10002/updateNickName'})"
              class="v-filed"
              v-model="userInfo.nickName"
              input-align="right"
            />
          </div>
        </div>
      </van-cell>
      <van-cell>
        <div class="clearfix">
          <div class="fl v-cell-text">简介</div>
          <div class="fl v-cell-textarea">
            <textarea
              @change="updateUserInfo({key: 'desc', value: userInfo.desc, url: 'http://www.kangliuyong.com:10002/updateDesc'})"
              class="textarea"
              v-model="userInfo.desc"
              maxlength="15"
            ></textarea>
            <div class="textarea_length">
              {{num}}/15
            </div>
          </div>
        </div>
      </van-cell>
    </van-cell-group>

    <div class="list-box">
      <van-cell-group>
        <van-cell title="修改密码" is-link @click="openPopup">
          <template #right-icon>
            <i class="fa fa-chevron-right" aria-hidden="true"></i>
          </template>
        </van-cell>

        <van-cell title="注销账号" is-link @click="destroy">
          <template #right-icon>
            <i class="fa fa-chevron-right" aria-hidden="true"></i>
          </template>
        </van-cell>
      </van-cell-group>
    </div>
    <div class="logout">
      <van-button color="#ff4f18" :block="true" type="danger" @click="logout">退出登录</van-button>
    </div>
    <van-popup v-model="isOpen" position="bottom">
      <div class="v-popup">
        <van-form @submit="commit">
          <van-field v-model="pwds.oldPassword" label="旧密码" placeholder="旧密码" />
          <van-field v-model="pwds.password" type="text" label="新密码" placeholder="新密码" />
          <div class="btn-box">
            <van-button block type="info" color="#ff4f18" native-type="submit">提交</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/css/account.less";
import validForm from "../assets/js/validForm";

export default {
  data() {
    return {
      userInfo: {},
      isOpen: false,
      pwds: {
        oldPassword: "",
        password: "",
      },
      num:''
    };
  },
  updated(){
      this.num = this.userInfo.desc.length
  },
  created() {
    //获取用户信息
    this.getUserInfo();
  },
  methods: {
    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "http://www.kangliuyong.com:10002/findAccountInfo",
        params: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "B001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //获取上传文件的base64
    uploadFile(file) {
      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "http://www.kangliuyong.com:10002/updateAvatar",
        data: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
          tokenString,
          imgType,
          serverBase64Img,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "H001") {
            this.userInfo.userImg = result.data.userImg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //修改昵称, 简介
    updateUserInfo(o) {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: o.url,
        data: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
          tokenString,
          [o.key]: o.value,
        },
      })
        .then((result) => {
          this.$toast.clear();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //显示修改密码框
    openPopup() {
      this.isOpen = true;
    },

    //提交修改密码
    commit() {
      if (!validForm.validUserForm(this.pwds)) {
        return;
      }

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "http://www.kangliuyong.com:10002/updatePassword",
        data: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
          tokenString,
          //新的密码
          password: this.pwds.password,
          //旧密码
          oldPassword: this.pwds.oldPassword,
        },
      })
        .then((result) => {
          this.$toast.clear();

          //关闭修改密码弹出框
          this.isOpen = false;
          if (result.data.code == "E001") {
            //清除token
            localStorage.removeItem("__Tk");
            this.$router.push({ name: "Login" });
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //退出登录
    logout() {
      localStorage.removeItem("__Tk");
      this.$router.push({ name: "Login" });
    },

    //注销
    destroy() {
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "一旦注销，数据无法恢复",
        })
        .then(() => {
          // on confirm
          //发起注销请求

          let tokenString = localStorage.getItem("__Tk");

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
          });

          this.axios({
            method: "POST",
            url: "http://www.kangliuyong.com:10002/destroyAccount",
            data: {
              appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
              tokenString,
            },
          })
            .then((result) => {
              this.$toast.clear();

              if (result.data.code == "G001") {
                //清除token
                localStorage.removeItem("__Tk");
                this.$router.push({ name: "Login" });
              } else {
                this.$toast(result.data.msg);
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch((err) => {});
    },
    back() {
      this.$router.go(-1);
    },
  },
};
</script>